import simpleAddress from "@/components/simple-address/simple-address.nvue"		
import backModal from '../../components/modal/backModal.vue'
export default {
	data() {
		return {
			name:'',
			phone:'',
			area:'请选择省市区',
			areaChoose:false,
			cityPickerValueDefault: [0, 0, 1],
			detailAddress:'',
			addressId:'',
			isDefault:0,
			isChecked:false,
			isSumbit:true,
			backShow:false
		};
	},
	components:{
		simpleAddress,backModal
	},
	methods:{
		openAddres() {
			this.$refs.simpleAddress.open();
		},
		onConfirm(e) {
			console.log(e);
			this.area = e.label
			this.areaChoose = true
		},
		switchChange(e){
			console.log(e);
			this.isChecked = e.target.value
			if(this.isChecked){
				this.isDefault = 1
			}else{
				this.isDefault = 0
			}
		},
		async sumbitAddressInfo(){
			if(!this.name || !this.phone || !this.area || !this.detailAddress) return uni.showToast({icon:'none',title:'请填写完整！'})
			if (!this.$u.test.mobile(this.phone)) return uni.showToast({icon: 'none',title: '请检查手机号是否输入正确'})
			if(!this.isSumbit) return
			this.isSumbit = false
			setTimeout(()=>{
				this.isSumbit = true
			},1000)
			if(this.addressId){
				let res = await this.$fetch(this.$api.editAddress,{
					userName:this.name,phonenumber:this.phone,province:this.area,isdefault:this.isDefault,detail:this.detailAddress,id:this.addressId},'GET')
				uni.showToast({
					icon:'none',
					title:res.msg
				})
				if(res.code == 0){
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					},1000)
				}
			}else{
				let res = await this.$fetch(this.$api.addAddress,{
					userName:this.name,phonenumber:this.phone,province:this.area,isdefault:this.isDefault,detail:this.detailAddress},'GET')
				uni.showToast({
					icon:'none',
					title:res.msg
				})
				if(res.code == 0){
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					},1000)
				}
			}
			
		},
		// 删除收货地址
		handleToDelAddrss(){
			if(!this.isSumbit) return
			this.isSumbit = false
			setTimeout(()=>{
				this.isSumbit = true
			},1000)
			uni.showModal({
				title:'删除地址',
				content:'确定要删除地址吗？',
				success: async (res) => {
					if(res.confirm){
						let res = await this.$fetch(this.$api.delAddress,{id:this.addressId},"GET")
						console.log(res);
						uni.showToast({
							icon:'none',
							title:res.msg
						})
						if(res.code == 0){
							setTimeout(()=>{
								uni.navigateBack({
									delta:1
								})
							},1000)
						}
					}
				}
			})
		},
		// 关闭弹窗
		modalClose(data){
			this.backShow = false
		},
		// 确认返回
		confirmBack(data){
			this.backShow = false
			if(data){
				uni.navigateBack({
					delta:1
				})
			}
		},
		// 获取收货地址详情
		async getAddressDetail(){
			let res = await this.$fetch(this.$api.getAddress,{id:this.addressId},'GET')
			console.log(res);
			this.name = res.data.userName
			this.phone = res.data.phonenumber
			this.area = res.data.province
			this.detailAddress = res.data.detail
			this.isDefault = res.data.isdefault
			if(this.isDefault == 1){
				this.isChecked = true
			}else{
				this.isChecked = false
			}
			this.areaChoose = true
		}
	},
	onLoad(options) {
		if(options.id){
			this.addressId = options.id
			uni.setNavigationBarTitle({
				title:'编辑收货地址'
			})
			this.getAddressDetail()
		}
	},
	onBackPress(options) {
		if(options.from === 'backbutton'){
			//添加收货地址时且有填写了信息的情况下
			if((!this.addressId)&&(this.name || this.phone || this.areaChoose || this.detailAddress)){
				this.backShow = true
				return true
			}
		}
	}
}
